import config from '../../config/configOP.json'
export class Detection {//
    constructor() {
        this.dectionURL=config.src.Detection.urlDetectionServer
        this.date=[
            new Date().getMonth(),
            new Date().getDate(),
            new Date().getHours(),
            new Date().getSeconds(),
            new Date().getMilliseconds()
        ]
        // this.time0=performance.now()

        this.count_p2p=0//P2P
        this.count_server=0//
        
        this.close=false
        this.pack_circumstances={}
        var scope=this

        this.testTime=60//window.param.testTime;//
        this.frameCount=0;//
        function testFrame(){
            scope.frameCount++
            requestAnimationFrame(testFrame);
        }testFrame()
        setTimeout(()=>{
            console.log("end")
            scope.finish()
        },scope.testTime*1000)
    }
    receivePack(type){
        if(type=="p2p")this.count_p2p++
        else this.count_server++
    }
    finish(){
        console.log(
            "count_p2p:"   ,this.count_p2p,
            "count_server:",this.count_server
        )
        this.close=true
        var data={
            count_p2p  :this.count_p2p,
            count_server:this.count_server,

            url:window.location.href,//
            date:this.date,         //
            frameCount:this.frameCount,//
            testTime:this.testTime,//
        }
        var oReq = new XMLHttpRequest();
        oReq.open("POST", this.dectionURL, true);
        oReq.responseType = "arraybuffer";
        oReq.onload = function () {//
            var unitArray=new Uint8Array(oReq.response) //unit8Array
            var str=String.fromCharCode.apply(null,unitArray)//
            console.log(str)
            // alert("")
            //window.opener = null;//
            //window.close();////
            // window.location.href="http://58.34.91.211:28081/?scene=KaiLiNan&useP2P=true&useP2P=true&needDetection=true&onlyP2P=true"
        };
        oReq.send(JSON.stringify(data));//
    }
}